.square {
  margin: 12px 30px;
  padding: 10px 5px;
  max-width: 250px;
  background-color: #ffffff;
  border: 4px solid #bbbbbb;
  cursor: pointer;
  -webkit-box-shadow: 6px 6px 3px #999999;
          box-shadow: 6px 6px 3px #999999; 
}
img {
  width: 130px;
  height: 170px;
}

@-webkit-keyframes star {
  0% {border: 4px solid #FFFF00;}
  10% {border: 4px solid #FFFF00;}
  25% {border: 4px solid #FFAA00;}
  50% {border: 4px solid #FF00FF;}
  75% {border: 4px solid #0000FF;}
  100% {border: 4px solid #00FF00;}
}

@keyframes star {
  0% {border: 4px solid #FFFF00;}
  10% {border: 4px solid #FFFF00;}
  25% {border: 4px solid #FFAA00;}
  50% {border: 4px solid #FF00FF;}
  75% {border: 4px solid #0000FF;}
  100% {border: 4px solid #00FF00;}
}

.square:hover {
  -webkit-animation-name: star;
          animation-name: star;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}



